html {
  box-sizing: border-box;
  font-size: 10px;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  background-color: #3bc1ac;
  background-image: radial-gradient(circle at 100% 150%, #3bc1ac 24%, #42d2bb 25%, #42d2bb 28%, #3bc1ac 29%, #3bc1ac 36%, #42d2bb 36%, #42d2bb 40%, rgba(0, 0, 0, 0) 40%, rgba(0, 0, 0, 0)), radial-gradient(circle at 0 150%, #3bc1ac 24%, #42d2bb 25%, #42d2bb 28%, #3bc1ac 29%, #3bc1ac 36%, #42d2bb 36%, #42d2bb 40%, rgba(0, 0, 0, 0) 40%, rgba(0, 0, 0, 0)), radial-gradient(circle at 50% 100%, #42d2bb 10%, #3bc1ac 11%, #3bc1ac 23%, #42d2bb 24%, #42d2bb 30%, #3bc1ac 31%, #3bc1ac 43%, #42d2bb 44%, #42d2bb 50%, #3bc1ac 51%, #3bc1ac 63%, #42d2bb 64%, #42d2bb 71%, rgba(0, 0, 0, 0) 71%, rgba(0, 0, 0, 0)), radial-gradient(circle at 100%, #42d2bb 5%, #3bc1ac 6%, #3bc1ac 15%, #42d2bb 16%, #42d2bb 20%, #3bc1ac 21%, #3bc1ac 30%, #42d2bb 31%, #42d2bb 35%, #3bc1ac 36%, #3bc1ac 45%, #42d2bb 46%, #42d2bb 49%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0)), radial-gradient(circle at 0, #42d2bb 5%, #3bc1ac 6%, #3bc1ac 15%, #42d2bb 16%, #42d2bb 20%, #3bc1ac 21%, #3bc1ac 30%, #42d2bb 31%, #42d2bb 35%, #3bc1ac 36%, #3bc1ac 45%, #42d2bb 46%, #42d2bb 49%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0));
  background-size: 100px 50px;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  display: flex;
}

.voiceinator {
  width: 50rem;
  z-index: 1;
  background: #fff;
  border-radius: 1rem;
  margin: 0 auto;
  padding: 2rem;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 5px 5px rgba(0, 0, 0, .1);
}

h1 {
  width: calc(100% + 4rem);
  text-align: center;
  text-shadow: 3px 3px #f3c010;
  background: #ffc600;
  border-bottom: 5px solid #f3c010;
  margin: -2rem 0 2rem -2rem;
  padding: .5rem;
  font-family: Pacifico, cursive;
  font-size: 5rem;
  font-weight: 100;
}

.voiceinator input, .voiceinator button, .voiceinator select, .voiceinator textarea {
  width: 100%;
  background: #f7f7f7;
  border: 0;
  outline: 0;
  margin: 10px 0;
  padding: 10px;
  font-size: 2rem;
  display: block;
}

textarea {
  height: 20rem;
}

.voiceinator button {
  width: 49%;
  float: left;
  cursor: pointer;
  background: #ffc600;
  border: 0;
  border-bottom: 5px solid #f3c010;
  margin-bottom: 0;
  font-family: Pacifico, cursive;
  font-size: 2rem;
  position: relative;
}

.voiceinator button:active {
  top: 2px;
}

.voiceinator button:nth-of-type(1) {
  margin-right: 2%;
}

